<template>

      <div class="cover" ref="coverbox">

        <div class="loaderbox">
            <div class="loader-left" :class="{endLeftLoader:endLeft}"></div>
            <div class="loader-right" :class="{endRightLoader:endRight}"></div>
              <div class="loader-circe" v-if="isLoading">
                <div class="loader loader1"></div>
                <div class="loader loader2"></div>
                <div class="loader loader3"></div>
              </div>
        </div>

        <div class="cover1">
          <div class="mask"></div>
          <div class="bg-swiper">

          </div>

          <div class="title">
            <div class="circleLight" @mousemove="movecircleLightBox($event)">
              <p>Hello,I'm Eliden</p>
              <p>我叫何鹏飞</p>
              <p>一名前端开发工程师</p>
              <p>这是我的个人博客</p>
            </div>
          </div>
          <div class="arrow-down" @click.stop="toNextCover"><i class="iconfont icon-xiajiantou"></i></div>
        </div>

        <div class="cover2">
          <div class="topbar">
            <div class="container"> <p class="name">Eliden·何鹏飞</p></div>
          </div>
          <div class="container">

              <h2>真正的快乐</h2>
              <div class="row-line"></div>
              <p class="describe">
                One needs 3 things to be truly happy living in the world: some thing to do, some one to love, some thing to hope for.
                <br>
                要得到真正的快乐，我们只需拥有三样东西：有想做的事，有值得爱的人，有美丽的梦。
              </p>

              <div class="list clear">

                <a href="https://eliden123.github.io/" alt="文章列表">
                  <div class="list-libox">
                    <div class="list-li">
                      <div class="iconbox-icon"><i class="iconfont icon-navicon-wzgl"></i></div>
                      <div class="iconbox-text">
                        <h3>文章列表</h3>
                        <p class="liTitle">所有文章列表</p>
                      </div>
                    </div>
                  </div>
                </a>

                <a href="https://eliden123.github.io/categories/" alt="文章分类列表">
                  <div class="list-libox">
                    <div class="list-li">
                      <div class="iconbox-icon"><i class="iconfont icon-fenlei"></i></div>
                      <div class="iconbox-text">
                        <h3>文章分类</h3>
                        <p class="liTitle">文章分类列表</p>
                      </div>
                    </div>
                  </div>
                </a>

                <div class="list-libox" @click.stop="toPhotos">
                  <div class="list-li">
                    <div class="iconbox-icon"> <i class="iconfont icon-xiangce"></i>  </div>
                    <div class="iconbox-text">
                      <h3>我的相册</h3>
                      <p class="liTitle">查看我的相册</p>
                    </div>
                  </div>
                </div>

                <a href="https://eliden123.github.io/about/">
                  <div class="list-libox">
                    <div class="list-li">
                      <div class="iconbox-icon"><i class="iconfont icon-guanyuwomen"></i></div>
                      <div class="iconbox-text">
                        <h3>有关于我</h3>
                        <p class="liTitle">获取我的信息</p>
                      </div>
                    </div>
                  </div>
                </a>

              </div>
          </div>
          <div class="footer">
            <div class="copyright">
                <p>© 2018 | Eliden</p>
            </div>
          </div>

        </div>

      </div>

</template>

<script>
export default {
  name: 'Cover',
  data () {
    return {
      isLoading: true,
      endLeft: false,
      endRight: false
    }
  },
  methods: {
    toNextCover () {
      const target = this.$refs.coverbox.offsetHeight

      const timer = setInterval(() => {
        let current = this.getScrollTop()

        let speed = (target - current) / 8

        if (current < target - 10) {
          window.scrollTo(0, current + speed)
        } else if (current < target) {
          window.scrollTo(0, target)
        } else {
          clearInterval(timer)
        }
      }, 20)
    },
    toPhotos () {
      this.$router.push({name: 'photos'})
    },
    getScrollTop () {
      var scrollTop = 0
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop
      } else if (document.body) {
        scrollTop = document.body.scrollTop
      }
      return scrollTop
    },
    movecircleLightBox (e) {
      console.log(e)
      console.log(e.offsetX)
      console.log(e.offsetY)
    },
    endLoader () {
      setTimeout(() => {
        this.isLoading = false
        this.endLeft = true
        this.endRight = true
      }, 1000)
    },
    preload () {
      setTimeout(function () {
        // XHR to request a JS and a CSS
        // var xhr = new XMLHttpRequest();
        // xhr.open('GET', 'http://domain.tld/preload.js');
        // xhr.send('');
        // xhr = new XMLHttpRequest();
        // xhr.open('GET', 'http://domain.tld/preload.css');
        // xhr.send('');
        // preload image
        new Image().src = 'https://s1.ax1x.com/2018/09/19/ieODfK.jpg'
      }, 1)
    }
  },
  beforeCreate () {
    this.preload()
  },
  mounted () {
    this.endLoader()
  }
}
</script>

<style scoped lang="stylus">
/*整版样式*/
.cover
  position: absolute
  width: 100%
  height: 100%
  min-height 566px
  margin:0
  padding:0

  //loader部分
.loader-left, .loader-right
  position: absolute
  width: 50%
  height: 100%
  top: 0
  z-index  10
  background-color: #A5BDBD
  display block
.loader-left
  left: 0
.loader-right
  right: 0
.loader-left.endLeftLoader
  animation endLoader 0.7s 0.3s ease
  animation-fill-mode forwards
.loader-right.endRightLoader
  animation endLoader 0.7s 0.3s ease
  animation-fill-mode forwards
@keyframes endLoader
{
0% {width:50%;display:block}
100% {width:0;display:none}
}

/*lorder圆环*/
.loader
  position: fixed
  top: 50%
  left: 50%
  z-index 11
  border-radius: 50%;
  border: 3px solid transparent;
.loader1
  width: 1.6rem
  height: 1.6rem
  margin-top: -.8rem
  margin-left: -.8rem
  border-top-color: #FFF;
  animation: spin 2s linear  infinite;
.loader2
  width: 1.4rem
  height: 1.4rem
  margin-top: -.7rem
  margin-left: -.7rem
  border-bottom-color: #FFF;
  animation: spin 1.6s linear infinite;
.loader3
  width: 1.2rem
  height: 1.2rem
  margin-top: -.6rem
  margin-left: -.6rem
  border-right-color: #FFF;
  animation: spin 1s linear infinite;

@keyframes spin
{
0%   {transform:rotate(0deg)}
100% {transform:rotate(360deg)}
}

/*版面*/
.cover1
  width: 100%
  height: 100%
  //background:#EFCCAC url("https://s1.ax1x.com/2018/09/19/ieOdT1.jpg") no-repeat center/cover
  background:#9EBAD3 url("https://s1.ax1x.com/2018/09/19/ieODfK.jpg") no-repeat center/cover
  //background:transparent url("https://s1.ax1x.com/2018/09/19/ieOBY6.jpg") no-repeat center/cover
.mask
  background-color: rgba(218, 218, 234, 0.2);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
.title
  color: black
  text-align: center
  position: relative
  left: 50%
  top: 50%
  transform: translate(-50%, -50%);
  width: 6rem

.circleLight
  height: 100%
  padding: .2rem 0
  border-radius .1rem

.title:hover .circleLight
  transition all 0.6s
  box-shadow:0px 0px 70px rgba(0, 0, 0, 0.4), 0px 0px 70px rgba(0, 0, 0, 0.4);
  background: radial-gradient(circle at 0px 0px, rgba(255, 255, 255,.3), transparent);

.title h1
  font: 700 .4rem/1.2 Raleway, sans-serif;
  letter-spacing:.05rem
  animation rotateInDownLeft 3.5s ease

.title p
  font-family: "微软雅黑",Raleway, "Times New Roman", Times, sans-serif;
  font-style: normal;
  text-transform: none;
  margin: 15px 0;
  font-size:.2rem
  font-weight:500
  letter-spacing .02rem

.cover .title p:nth-child(1)
  font-family: Times, sans-serif,"微软雅黑";
  letter-spacing .03rem
  font-size: .6rem;
  animation fadeInLeftBig 2.5s ease

.arrow-down
  width: .45rem
  height: .45rem
  border-radius 50%
  border:.02rem solid white
  position: absolute
  left: 50%
  transform: translate(-50%,0% );
  bottom:.5rem
  cursor pointer
  .iconfont
    width: .45rem
    height: .45rem
    font-size: .16rem !important
    display inline-block
    color: white
    position: absolute
    left:50%
    margin-left:-.225rem
    text-align: center
    top:0
    opacity:0
    animation down 1s ease infinite;
.arrow-down:hover
  color: #fff;

@keyframes down
{
0%   {top:0px;}
30%  {opacity:1;}
50%  {opacity:1;}
100% {top:.28rem;opacity:0;}
}

/*第二版面*/
.cover2
  width: 100%
  height: 100%
  background-color:#F5F5F5
  position: relative
.topbar
  height: .5rem
  line-height: .5rem
  background-color: #fff
.name
  font-weight: 700;
  font-size:.22rem;
  font-family: Lobster, cursive;
  text-align: left
  padding-left:.2rem

.row-line {
  background: rgba(135, 135, 135, 0.5);
  width: 100px;
  height: 1px;
  margin: 30px auto;
}
.container
  text-align: center
.container h2
  font: 700 .3rem/1.2 Raleway, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0 5px;
  color: #222;
  padding-top:.8rem
.describe
  font-family: Raleway, "Times New Roman", Times, sans-serif;
  font-style: normal;
  font-size: .18rem;
  line-height:2em
  color: #666
  font-weight:300
.list
  padding-top: .6rem

.list-libox
  width: 25%
  padding:0 .12rem
  float: left
  cursor pointer

.list-li
  background-color: #fff
  border-bottom: 1px solid #d4d4d4;
  text-align: center;
  padding: 48px 20px;
  margin: 0 0 20px;
  font-size: .13rem
  border-radius 8px
  box-shadow: 0px 10px 18px -4px rgba(0, 0, 0, 0.2);
  transition:all 0.3s
.list-li:hover
  box-shadow: 0px 10px 18px -6px rgba(0, 0, 0, 0.4);
  margin-top:-.2rem

.iconbox-icon
  margin-bottom:.2rem
.iconbox-icon .iconfont
  font-size:.35rem
  color: #222;

.iconbox-icon .icon-navicon-wzgl
  font-size:.37rem

.iconbox-text h3
  color: #222;
  font: 700 .13rem/1.2 Raleway, sans-serif;
  margin-bottom:.16rem
  position: relative
.iconbox-text h3::after
  content: "";
  position: absolute;
  left: 50%;
  bottom: -6px;
  width: 0;
  height: 2px;
  background: #666;
  transition: all .3s;
.iconbox-text h3:hover:after
  width: 100%;
  left: 0;
  right: 0;

.iconbox-text .liTitle
  font: 300 .14rem/1.8 'Raleway', sans-serif;
  color: #666;
.copyright
  width: 100%
  position: absolute
  font-size: .18rem
  bottom:.25rem
  text-align: center
</style>



